import React from "react";
import Aux from "../../../hoc/Auxiliary/Auxiliary";
import CheckBoxesGroup from "./checkBoxesGroup/checkBoxesGroup";
import PropTypes from "prop-types";

const changeBornSurvive = (props) => (
  <tr>
    <td>
      <CheckBoxesGroup
        title="born"
        updatelistfunction={props.updateBornHandler}
        checkedValuesList={props.bornList}
      />
    </td>
    <td>
      <CheckBoxesGroup
        title="survive"
        updatelistfunction={props.updateSurviveHandler}
        checkedValuesList={props.surviveList}
      />
    </td>
  </tr>
);

export default changeBornSurvive;
