import React, { Component } from "react";
import Aux from "../hoc/Auxiliary/Auxiliary";
import Board from "../components/Board/Board";
import PatternFactory from "./Patterns/PatternFactory";
import BoardConrtols from "../components/BoardControls/BoardConrtols";
import AboutAndRules from "./AboutAndRules/AboutAndRules";
import { Header } from "semantic-ui-react";
const STATUS = {
  ALIVE: 1,
  DEAD: 0,
};

class GameOfLife extends Component {
  state = {
    size: { rows: 50, cols: 70 },
    grid: Array(50)
      .fill()
      .map(() => Array(70).fill(0)),
    born: [3],
    survive: [2, 3],
    PatternFactory: new PatternFactory(),
    run: false,
    Delay: 250,
    emptyGrid: Array(50)
      .fill()
      .map(() => Array(70).fill(0)),
  };

  updateBornHandler = (n) => {
    let newBorn = [...this.state.born];
    let i = newBorn.indexOf(n);
    if (i === -1) {
      newBorn.push(n);
    } else newBorn.splice(i, 1);
    this.setState({ born: newBorn });
  };

  updateSurviveHandler = (n) => {
    let newSurvive = [...this.state.survive];
    let i = newSurvive.indexOf(n);
    if (i === -1) {
      newSurvive.push(n);
    } else newSurvive.splice(i, 1);
    this.setState({ survive: newSurvive });
  };

  oneCycle = () => {
    let a = performance.now();
    let newGrid = Array(50)
      .fill()
      .map(() => Array(70).fill(0));
    let count;
    for (let i = 0; i < this.state.size.rows; i++) {
      for (let j = 0; j < this.state.size.cols; j++) {
        count = this.countNeighbours(i, j);
        if (this.state.grid[i][j] === STATUS["ALIVE"]) {
          if (!this.state.survive.includes(count)) {
            newGrid[i][j] = STATUS["DEAD"];
          } else {
            newGrid[i][j] = STATUS["ALIVE"];
          }
        } else {
          if (this.state.born.includes(count)) {
            newGrid[i][j] = STATUS["ALIVE"];
          }
        }
      }
    }
    this.setState({ grid: newGrid });
    let b = performance.now();
    console.log("OneCycle: oneCycle took " + (b - a) + " ms");
  };

  countNeighbours = (row, col) => {
    let sum = 0;
    for (let i = -1; i <= 1; i++) {
      if (row + i >= 0 && row + i < this.state.size.rows) {
        for (let j = -1; j <= 1; j++) {
          if (col + j >= 0 && col + j < this.state.size.cols) {
            sum = sum + this.state.grid[row + i][col + j];  
          }
        }
      }
    }
    return sum - this.state.grid[row][col];
  };

  onClickSquareHandler = (i, j) => {
    const currState = [...this.state.grid];
    currState[i][j] = 1 - currState[i][j];
    this.setState({ grid: currState });
  };

  resizeColsHandler = (addition) => {
    let newSize = { ...this.state.size };
    newSize.cols = newSize.cols + addition;

    let newGrid = [...this.state.grid];
    for (let i = 0; i < addition; i++) {
      for (let j = 0; j < this.state.size.rows; j++) {
        newGrid[j].push(0);
      }
    }
    this.setState({ size: newSize, grid: newGrid });
  };

  resizeRowsHandler = (addition) => {
    let newSize = { ...this.state.size };
    newSize.rows = newSize.rows + addition;

    let newGrid = [...this.state.grid, Array(newSize.cols).fill(0)];

    this.setState({ size: newSize, grid: newGrid });
  };

  resizeRowAndColHandler = (addition) => {
    this.resizeRowsHandler(addition);
    this.resizeColsHandler(addition);
  };

  placePatternsHadler = (pattern) => {
    let newGrid = [...this.state.grid];
    let block = this.state.PatternFactory.buildAPatternByName(pattern);
    const starting_row = 5;
    const starting_col = 5;
    let k = 0;
    let l = 0;
    for (let i = 5; i < starting_row + block.length; i++) {
      for (let j = 5; j < block[l].length + starting_col; j++) {
        newGrid[i][j] = block[l][k];
        k++;
      }
      k = 0;
      l++;
    }
    this.setState({ grid: newGrid });
  };

  resetBoard = () => {
    this.stopGame();
    let newGrid = Array(50)
      .fill()
      .map(() => Array(70).fill(0));
    this.setState({ grid: newGrid });
  };

  playGame = () => {
    this.setState({ run: true });
    this.timerID = setInterval(() => this.oneCycle(), this.state.Delay);
  };

  stopGame = () => {
    this.setState({ run: false });
    clearInterval(this.timerID);
  };

  setRandomBoard = () => {
    let newGrid = Array(50)
      .fill()
      .map(() => Array(70).fill(0));
    for (let i = 0; i < newGrid.length; i++) {
      for (let j = 0; j < newGrid[i].length; j++) {
        newGrid[i][j] = Math.floor(Math.random() * 2);
      }
    }
    this.setState({ grid: newGrid });
  };

  render() {
    return (
      <Aux>
        <Header
          as="h1"
          color="violet"
          dividing
          textAlign="center"
          attached="bottom"
          content="Conway's Game of Life"
          subheader="By Itzik Fadida, May 2020"
        />
        <Board
          grid={this.state.grid}
          squareOnClickHandler={this.onClickSquareHandler}
          oneCycle={this.oneCycle}
        />



        <BoardConrtols className="Controls"
        updateBorn={this.updateBornHandler}
        bornList={this.state.born}
        updateSurvive={this.updateSurviveHandler}
        surviveList={this.state.survive}
        addRow={() => this.resizeRowsHandler(1)}
        addCol={() => this.resizeColsHandler(1)}
        addRowCol={() => this.resizeRowAndColHandler(1)}
        resetBoard={this.resetBoard}
        playGame={this.playGame}
        stopGame={this.stopGame}
        setRandomBoard={this.setRandomBoard}
        placePatternsHadler={this.placePatternsHadler}
        oneRound={this.oneCycle}
      />
      

      </Aux>
    );
  }
}

export default GameOfLife;
