import React from "react";
import { Tab } from "semantic-ui-react";
import "./AboutAndRules.css";
const panes = [
  {
    menuItem: "About Conway's Game Of Life",
    render: () => (
      <Tab.Pane attached={false}>
        <div className="wrapped">
          <p>
            The Game of Life is a cellular automaton devised by the British
            mathematician, John Horton Conway in 1970.
          </p>
          <p>
            It is a zero-player game, meaning that its evolution is determined
            by its initial state, requiring no further input. One interacts with
            the Game of Life by creating an initial configuration and observing
            how it evolves. It is Turing complete and can simulate a universal
            constructor
          </p>
          <p>
            The universe of the Game of Life is an infinite, two-dimensional
            orthogonal grid of square cells, each of which is in one of two
            possible states, LIVE or DEAD.
          </p>
          <p>
            Every cell interacts with its eight neighbours, which are the cells
            that are horizontally, vertically, or diagonally adjacent. At each
            step in time, the following transitions occur:
          </p>
          <ol>
            <li>
              Any live cell with fewer than X live neighbours dies, as if by
              underpopulation.
            </li>
            <li>
              Any live cell with X live neighbours lives on to the next
              generation.
            </li>
            <li>
              Any live cell with more than X live neighbours dies, as if by
              overpopulation.
            </li>
            <li>
              Any dead cell with exactly Y live neighbours becomes a live cell,
              as if by reproduction.
            </li>
          </ol>
          <p>X, Y reffering to groups of numbers [1 to 8]</p>
        </div>
      </Tab.Pane>
    ),
  },
  {
    menuItem: "Guide, How to Play",
    render: () => (
      <Tab.Pane attached={false}>
        <div className="wrapped">
          <p>
            Live cell will be colored <strong className="green">Green</strong>{" "}
            while Dead cell will be colored{" "}
            <strong className="blue">Blue</strong>.
          </p>
          <p>
            Clicking on cell will turn it status to the other status, meaning
            Dead cell will become Alive and Live cell will become Dead
          </p>
          <p>
            Using "Random" bottom will set the board randomly with chance of 50%
            to each cell to alive.
          </p>
          <p>
            Using "Place A Pattern" bottom will allow you to set a special
            pattern.
          </p>
          <p>"One Round" will set the new generation according to the rules.</p>
          <p>"Start Evolve" will run new generations.</p>
          <p>"Pause" will puase making new generations.</p>
          <p>"Clean Board" will set all cells to Dead.</p>
          <p>
            "Born" and "Survive" will set the amount of alive neighbours needed
            for the following generations.
          </p>
        </div>
      </Tab.Pane>
    ),
  },
  {
    menuItem: "About Me & Project",
    render: () => (
      <Tab.Pane attached={false}>
        <div className="wrapped">
          <p>
          Hello, My name is Itzik from Israel <i class="israel flag"></i>
          </p>
          <p>This is my first project programed with JavaScript and React framework.</p>
          <p>For styling this project I've used "Semantic UI" package.</p>
          <p>Hope you'll enjoy my work!</p>
        </div>
      </Tab.Pane>
    ),
  },
];
const aboutAndRules = (props) => (
  <Tab menu={{ secondary: true, pointing: true }} panes={panes} />
);

export default aboutAndRules;
