import React from "react";
import "./checkBoxesGroup.css";
import { Checkbox } from 'semantic-ui-react'

const NEIGHBOURS = [1, 2, 3, 4, 5, 6, 7, 8];

const checkBoxesGroup = (props) => {
  const options = NEIGHBOURS.map((n) => {
    return (
      <td key={"td" + n}>
        <div key={"div"+n} className="ui checkbox">
          <Checkbox
            key={"cb" + n}
            value={n}
            onClick={() => props.updatelistfunction(n)}
            label={n}
            checked={props.checkedValuesList.includes(n) ? true : false}
          />
        </div>
      </td>
    );
  });

  const firstRow = <tr>{options.splice(0, 4)}</tr>;
  const secondRow = <tr>{options.splice(0, 8)}</tr>;

  return (
    <table className={props.title}>
      <thead>
        <tr>
          <th colSpan={4}>
            {props.title[0].toUpperCase() +
              props.title.slice(1, props.title.lenght)}
          </th>
        </tr>
      </thead>
      <tbody>
        {firstRow}
        {secondRow}
      </tbody>
    </table>
  );
};

export default checkBoxesGroup;
