import React from "react";
import ChangeBornSurvive from "./BornSurviveControls/BornSurviveControls";
import Aux from "../../hoc/Auxiliary/Auxiliary";
import PropTypes from "prop-types";
import PatternsList from "./PatternsList/PatternsList";
import AboutAndRules from "../../containers/AboutAndRules/AboutAndRules";

const boardControls = (props) => (
  <Aux>
    <table>
    <tr>
    <td colSpan="2">
    <AboutAndRules/>
    <br/>
    </td></tr>
      <tr>
        <td colSpan="2">
          <button
            className="ui labeled icon teal button"
            onClick={props.oneRound}
          >
            <i class="step forward icon"></i>One Round
          </button>
          <button
            className="ui labeled icon blue button"
            onClick={props.playGame}
          >
            <i className="play icon"></i>Start Evolve
          </button>ְּ
          <button
            className="ui labeled icon olive button"
            onClick={props.stopGame}
          >ּ
            <i className="pause icon"></i>Pause Game
          </button>
        </td>
      </tr>
      <tr>
        <td colSpan="2">
          <button
            className="ui labeled icon purple button"
            onClick={props.setRandomBoard}
          >
            <i className="random icon"></i>Random
          </button>

          <PatternsList onChangeHandler={props.placePatternsHadler} />

          <button className="ui labeled icon red button" onClick={props.resetBoard}>
            <i className="trash alternate icon"></i>Clean Board
          </button>
        </td>
      </tr>
      <ChangeBornSurvive
      updateBornHandler={props.updateBorn}
      bornList={props.bornList}
      updateSurviveHandler={props.updateSurvive}
      surviveList={props.surviveList}
    />

    </table>
    
  </Aux>
);

export default boardControls;

boardControls.prototype = {
  updateBornHandler: PropTypes.func,
  bornList: PropTypes.array,
  updateSurviveHandler: PropTypes.func,
  surviveList: PropTypes.array,
};
