import React from "react";
import { Dropdown} from "semantic-ui-react";
import 'semantic-ui-css/semantic.min.css';

const availablePatterns = [
  { key: 1, text: "Block", value: "Block", size: "2x2"},
  { key: 2, text: "Beehive", value: "Beehive", size:"2x2" },
  { key: 3, text: "Blinker", value: "Blinker", size:"2x2"},
  { key: 4, text: "Glider", value: "Glider", size:"2x2"},
  { key: 5, text: "Gosper Glider Gun", value: "GosperGliderGun", size:"2x2"},
  { key: 6, text: "Pulsar", value: "Pulsar", size:"2x2"},
];
const patternsList = (props) => (
    <Dropdown
    text='Place A Pattern'
    floating
    labeled
    button
    icon='magic'
    className='icon violet'
    onChange={props.onChangeHandler}
  >
    <Dropdown.Menu>
      <Dropdown.Header content='Choose a pattern from the list below' />
      <Dropdown.Divider />
      {availablePatterns.map((p) => {
        return (<Dropdown.Item key={p.key} description={p.size} text={p.text} value={p.value} onClick={()=>{props.onChangeHandler(p.value)}}/>);
      })}
    </Dropdown.Menu>
  </Dropdown>
);

export default patternsList;
