import React, { Component } from "react";
import Aux from "../../hoc/Auxiliary/Auxiliary";
import Cell from "./Cell/Cell";
import BoardConrtols from "../BoardControls/BoardConrtols";
import "./Board.css";

class Board extends Component {

  render() {
    const rows = this.props.grid.map((row, i) => {
      return (
        <tr key={"row_" + i}>
          {row.map((col, j) => {
            return (
              <Cell
                key={i + "_" + j}
                status={this.props.grid[i][j] === 0 ? false : true}
                clicked={() => this.props.squareOnClickHandler(i, j)}
              />
            );
          })}
        </tr>
      );
    });
    return (
      <Aux>
        <table className="Board">
          <tbody>{rows}</tbody>
        </table>
      </Aux>
    );
  }
}

export default Board;
